import React  from 'react';
import { Link } from 'react-router-dom'
import { Autoplay } from 'swiper';
import { Swiper, SwiperSlide } from 'swiper/react/swiper-react.js';

import 'swiper/swiper.scss';
import 'swiper/modules/navigation/navigation.scss';
import 'swiper/modules/pagination/pagination.scss';
import img1 from '../../assets/images/box-item/img_item1.png'
import img2 from '../../assets/images/box-item/img_item2.png'
import img3 from '../../assets/images/box-item/img_item3.png'
import img4 from '../../assets/images/box-item/img_item4.png'
import img5 from '../../assets/images/box-item/img_item5.png'
import img6 from '../../assets/images/box-item/img_item6.png'
import img7 from '../../assets/images/box-item/img_item7.png'
import img8 from '../../assets/images/box-item/img_item8.png'
import img9 from '../../assets/images/box-item/img_item9.png'
import img10 from '../../assets/images/box-item/img_item10.png'
import img11 from '../../assets/images/box-item/img_item11.png'
import img12 from '../../assets/images/box-item/img_item12.png'
import img13 from '../../assets/images/box-item/img_item13.png'
import img14 from '../../assets/images/box-item/img_item14.png'
import shape1 from '../../assets/images/backgroup-secsion/bg-gradient1.png'
import shape2 from '../../assets/images/backgroup-secsion/bg-gradient2.png'
import shape3 from '../../assets/images/backgroup-secsion/bg-gradient3.png'

const SliderStyle4 = () => {
    
    return (
        <div>
            <section className="flat-title-page style3 mainslider">
                <img className="bgr-gradient gradient1" src={shape1} alt="Astro" />
                <img className="bgr-gradient gradient2" src={shape2} alt="Astro" />
                <img className="bgr-gradient gradient3" src={shape3} alt="Astro" />
                <div className="overlay"></div>
                <div className="themesflat-container ">
                        <div className="wrap-heading flat-slider flex">
                            <div className="content">
                                <h2 className="heading mt-15">Discover, find,
                                </h2>	
                                <h1 className="heading mb-style"><span className="tf-text s1">Sell extraordinary</span>                                          
                                </h1>
                                <h1 className="heading">AstroMarket</h1>
                                <p className="sub-heading mt-19 mb-40">Marketplace for generative nft collections non fungible token NFTs
                                </p>
                                <div className="flat-bt-slider flex style2">
                                    <Link to="/collections" className="sc-button header-slider style style-1 rocket fl-button pri-1"><span>Explore
                                    </span></Link>
                                    <a href="https://astrogen.io" target="_blank" className="sc-button header-slider style style-1 note fl-button pri-1"><span>Create
                                    </span></a>
                                </div>
                            </div>
                            <Swiper
                                modules={[ Autoplay ]}
                                direction={"vertical"}
                                spaceBetween={25}
                                slidesPerView={5}
                                loop
                                autoplay={{
                                    delay: 1,
                                    disableOnInteraction: false,
                                    pauseOnMouseEnter: true,
                                }}
                                speed= {2000}
                            >
                                <SwiperSlide><img src={img1} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img2} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img4} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img3} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img6} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>

                            </Swiper>
                            <Swiper
                                modules={[ Autoplay ]}
                                direction={"vertical"}
                                spaceBetween={25}
                                slidesPerView={5}
                                loop
                                autoplay={{
                                    delay: 1,
                                    disableOnInteraction: false,
                                    pauseOnMouseEnter: true,
                                }}
                                speed= {1800}
                            >
                                <SwiperSlide><img src={img7} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img8} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img9} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img10} alt="Astro" style={{maxWidth:"240px"}} /></SwiperSlide>
                                <SwiperSlide><img src={img11} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>

                            </Swiper>
                            <Swiper
                                modules={[ Autoplay ]}
                                direction={"vertical"}
                                spaceBetween={25}
                                slidesPerView={5}
                                loop
                                autoplay={{
                                    delay: 1,
                                    disableOnInteraction: false,
                                    pauseOnMouseEnter: true,
                                }}
                                speed= {2200}
                            >
                                <SwiperSlide><img src={img5} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img12} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img13} alt="Astro" style={{maxWidth:"240px"}} /></SwiperSlide>
                                <SwiperSlide><img src={img3} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>
                                <SwiperSlide><img src={img14} alt="Astro" style={{maxWidth:"240px"}}/></SwiperSlide>

                            </Swiper>
                        </div>
                        
                    </div>
            </section>
        </div>
    );
}


export default SliderStyle4;
