import React, { useState, useEffect } from 'react';
import ExploreItem from './ExploreItem';
import SideBar from '../../../components/layouts/home-8/SideBar';
import { InputGroup, FormControl, Button, Dropdown, DropdownButton } from 'react-bootstrap';
import Select from '../../Common/Select';

const Explore = props => {
    const sideData = props.data;
    const [name, setname] = useState('');

    const [collectionSelect, SetCollectionSelect] = useState('Price: Low To High ');
    const [visible, setvisible] = useState(false);
    const logsetvisible = (ev) => {
        console.log(ev);
        setvisible(ev)
    }
    const [ collectionFilter, setcollectionFilter ] = useState({});
    const [filterchanged, setfilterchanged] = useState(1)
    const [traitfilter, settraitfilter] = useState(null)
    const [on_sale, seton_sale] = useState(false)
    const [buyfloor, setbuyfloor] = useState(false);

    const clear = () => {
        let newfilterdata = [];
        Object.entries(sideData).forEach(([key, value]) => {
            let contents = []
            Object.entries(value).forEach(([key1, value1]) => {
                contents.push(0)
            });
            newfilterdata.push(contents)
        });
        setcollectionFilter(newfilterdata)
        setfilterchanged(Math.random()/9 + 1)
        settraitfilter(null);
    }

    const done = () => {
        console.log('done')
        setvisible(false)
        setfilterchanged(Math.random()/9 + 1)
        let index=0, index1=0;
        let newfilterdata = []
        Object.entries(sideData).forEach(([key, value]) => {
            let singleFilterstr='';
            index1=0;
            Object.entries(value).forEach(([key1, value1]) => {
                // console.log(collectionFilter[index][index1])
                singleFilterstr += collectionFilter[index][index1] == 1 ? (singleFilterstr ? "||" + key1 : key1): "";
                index1++;
            });
            if(singleFilterstr){
                newfilterdata.push({
                    "trait_type" : key,
                    "value": singleFilterstr,
                });
            }
            index++;
        });
        let price = collectionSelect == 'Price: Low To High ' ? 1 : -1
        console.log(on_sale)
        settraitfilter({trait: newfilterdata, name: name, price: price, sale: on_sale});
    }

    const clickFilter = (i,j,checked) => {
        console.log('click',i,j, checked)
        let newfilterdata = collectionFilter;
        let topdata = newfilterdata[i]
        topdata.splice(j,1,checked ? 1 : 0)
        newfilterdata.splice(i,1,topdata)
        setcollectionFilter(newfilterdata)
        setfilterchanged(Math.random()/9 + 1)
    }

    useEffect(() => {
        clear();
        console.log(sideData)
    }, [ props ]);

    return (
        <section className="tf-explore tf-section pt-4">
            <div className="themesflat-container">
                <div className="row">
                    <div className="col-xl-3 col-lg-3 col-md-12 mb-5 stickyfilter" >
                        <SideBar data={sideData} visible={visible} setvisible={logsetvisible} clear={clear} done={done} clickFilter={clickFilter} collectionFilter={collectionFilter} setsale={seton_sale}/>
                    </div>
                    <button className='filterhoverbtn' onClick={() => logsetvisible(!visible)}><big><i className="fa fa-filter" aria-hidden="true" style={{ fontSize: '20px' }}></i> Filters</big></button>
                    <div className="col-xl-9 col-lg-9 col-md-12">
                        <div className='widget-search mt-2'>
                            <InputGroup className="mb-3">
                                <FormControl
                                placeholder="Please type user's name or address"
                                aria-label="Recipient's username"
                                aria-describedby="basic-addon2"
                                value={name}
                                onChange={(ev) => setname(ev.target.value)}
                                />
                                <Button variant="outline-secondary" onClick={done}>
                                    <i className="fa fa-search" aria-hidden="true"></i>
                                </Button>
                                <div className="mx-4">
                                    <Select value={collectionSelect} setValue={SetCollectionSelect} options={['Price: Low To High ', 'Price: High To Low ']}/>  
                                </div>
                                <Button variant="outline-secondary" onClick={() => setbuyfloor(true)} style={{ borderRadius: '10px'}}>
                                    {/* <i className="fa fa-search" aria-hidden="true"></i> */}
                                    Buy Floor
                                </Button>
                            </InputGroup>
                        </div>
                        <ExploreItem traitfilter={traitfilter} contract_id={props.contract_id} buyfloor={buyfloor} />
                    </div>
                </div>
            </div>
        </section>
    );
}

export default Explore;
