import React from 'react';
import { Link } from 'react-router-dom'
import { Modal } from "react-bootstrap";

const CardModal = (props) => {
    
return (

    <Modal
      show={props.show}
      onHide={props.onHide}
    >
    <Modal.Header closeButton></Modal.Header>

    <div className="modal-body space-y-20 pd-40">
        <h3>Place a Bid</h3>
        <p className="text-center">You must bid at least <span className="price color-popup">4.89 NEAR</span>
        </p>
        <input type="number" className="form-control"
            placeholder="00.00 NEAR" />
        <div className="d-flex justify-content-between">
            <p> Service fee:</p>
            <p className="text-right price color-popup"> 10% </p>
        </div>
        <Link to="/#" className="btn btn-primary" data-toggle="modal" data-target="#popup_bid_success" data-dismiss="modal" aria-label="Close"> Place a bid</Link>
    </div>
    </Modal>
    
  );
};

export default CardModal;
