import React , { useState , useEffect, Fragment } from 'react';
import { Link, useNavigate } from 'react-router-dom'
import axios from 'axios';
import CardModal from '../CardModal';
import InfiniteScroll from 'react-infinite-scroll-component';
import Spinner from 'react-bootstrap/Spinner';
import { toast } from 'react-toastify';

const ExploreItem = ({traitfilter, contract_id, buyfloor }) => {
    const navigation = useNavigate();
    

    const TOKENS_PER_PAGE = 9;
    const [query, setQuery] = useState({page:0});
    const [tokens, setTokens] = useState([]);
    const [collectionId, setCollectionId] = useState("");
    const [loading, setLoading] = useState(false);
    const [hasMore, setHasMore] = useState(false); // Can load more tokens

    const [modalShow, setModalShow] = useState(false);
    
    const getTokens = async (query) => {
        try {
            if(contract_id=='') return
            const resCollections = await axios.get(`${process.env.REACT_APP_API_URL}/tokens`, {
                params: {
                    ...query.filters,
                    nft_contract_id: contract_id,
                    __skip: query.page * TOKENS_PER_PAGE,
                    __limit: TOKENS_PER_PAGE,
                    trait_filter: traitfilter?.trait,
                    name: traitfilter?.name,
                    price: traitfilter?.price,
                    itemstatus: traitfilter?.sale
                },
		    })

            setTokens(resCollections.data.data.results);
    
            if (resCollections.data.data.results.length >= TOKENS_PER_PAGE) setHasMore(true);
    
        } catch (error) {
            console.log(error);
            setTokens([]);
        }
    
        setLoading(false);
    };

    const fetchMoreTokens = async (query) => {
        try {
            const resCollections = await axios.get(`${process.env.REACT_APP_API_URL}/tokens`, {
                params: {
                    ...query.filters,
                    nft_contract_id: contract_id,
                    __skip: (query.page + 1) * TOKENS_PER_PAGE,
                    __limit: TOKENS_PER_PAGE,
                    trait_filter: traitfilter?.trait,
                    name: traitfilter?.name,
                    price: traitfilter?.price,
                    itemstatus: traitfilter?.sale
                },
		    })

            const data = await resCollections.data.data.results;
    
            setQuery({ ...query, page: query.page + 1 });
        
            if (data.length < TOKENS_PER_PAGE) setHasMore(false);
            setTokens([...tokens, ...data]);
        } catch (error) {
          console.log(error);
        }

    };

    useEffect(() => {
        setLoading(true);
        getTokens(query);
        setCollectionId(contract_id);
    }, [query.filters, contract_id, traitfilter]);

    const gotobuyfloor = async () => {
        try {
            if(contract_id=='') return
            const resCollections = await axios.get(`${process.env.REACT_APP_API_URL}/tokens`, {
                params: {
                    ...query.filters,
                    nft_contract_id: contract_id,
                    __skip: 0,
                    __limit: 1,
                    trait_filter: traitfilter?.trait,
                    name: traitfilter?.name,
                    price: 1,
                    itemstatus: true
                },
		    })
            let res = resCollections.data.data.results
            if(res && res.length == 0){
                // alert('a')
                toast('There is no sale nft!')
                return;
            }
            navigation(`/collection/${contract_id}/${res[0].token_id}`)
        } catch (error) {
            console.log(error);
            setTokens([]);
        }
    }

    useEffect(() => {
        if(!buyfloor) return;
        gotobuyfloor();
    }, [buyfloor]);

    return (
        <Fragment>
            <div className='explore'>
                <div className="box-epxlore">
                {loading && (
                    <Spinner animation="border" role="status" variant="primary" style={{width:"100px", height:"100px", borderWidth: "thick", position:"relative", left: "50%"}}>
                        <span className="visually-hidden">Loading...</span>
                    </Spinner>
                )}
                <InfiniteScroll
                    dataLength={tokens.length}
                    next={() => fetchMoreTokens(query)}
                    hasMore={hasMore}
                    loader={
                        <Spinner animation="border" role="status" variant="primary" style={{width:"40px", height:"40px", position:"relative", left: "50%"}}>
                            <span className="visually-hidden">Loading...</span>
                        </Spinner>
                    }
                    className="infinite-scroll-grid flex"
                    scrollThreshold={0.9}
                    style={{ height:'auto', overflow: 'hidden'}}
                >
                    {!loading && tokens.length>0 && 
                            tokens.map((item, index)=>(
                                <div className={`sc-card-product explode style2 mg-bt ${item.feature ? 'comingsoon' : '' } `} key={index}>
                                    <div className="card-media explore-item">
                                        <Link to={`/collection/${collectionId}/${item.token_id}`}><img src={item?.media_url?.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/") || item.metadata.image.replace("ipfs://", "https://cloudflare-ipfs.com/ipfs/")} alt="Axies" /></Link>
                                    </div>
                                    <div className="card-title">
                                        <h5><Link to={`/collection/${collectionId}/${item.token_id}`}>"{item.metadata.name || item.metadata.title}"</Link></h5>
                                    </div>
                                    <div className="card-bottom s tyle-explode">
                                        <div className="price">
                                            <span>Price</span>
                                            <div className="price-details">
                                                <h5>{item?.price}</h5>
                                                <span>= {""}</span>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            )  
                    )}
                </InfiniteScroll>
                </div>
            </div>
            <CardModal
                show={modalShow}
                onHide={() => setModalShow(false)}
            />
        </Fragment>
    );
}

export default ExploreItem;
