import React , {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import { InputGroup, Button, FormControl } from 'react-bootstrap';
import axios from 'axios'

const MyActivity = () => {
    const [data] = useState(
        [
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoa.near',
                issued_at: '2022-05-29T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoa.near',
                issued_at: '2022-05-29T20:00:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoa.near',
                issued_at: '2022-05-29T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoaafdasaa.near',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoaafdasaa.near',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoaafdasaa.near',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: '',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: null,
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoa.near',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoa.near',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoa.near',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
            {
                img: 'https://nearnaut.mypinata.cloud/ipfs/QmeJnA48tXgFXZEfVGn1mZPuesCwq9bMGa5McZ5nbfTiHE',
                title: "Uzmaki Naruto",
                from: 'gyeoulie.near',
                to: 'gyeoa.near',
                issued_at: '2022-05-02T20:47:09.759Z',
                price: '12.4353',
                type: 'nft_transfer'
            },
        ]
    )
    
    const [dataFilter, setdataFilter] = useState(
        [
            {
                icon: 'icon-fl-sort-filled',
                name: 'Listings',
                state: false
            },
            {
                icon: 'icon-fl-discount',
                name: 'Sold',
                state: false
            },
            {
                icon: 'icon-fl-logout',
                name: 'Transfer',
                state: false
            },
            {
                icon: 'icon-fl-star',
                name: 'Burns',
                state: false
            },
            {
                icon: 'icon-fl-credit-card',
                name: 'Bids',
                state: false
            },
        ]
    )

    const [visible , setVisible] = useState(5);
    const showMoreItems = () => {
        setVisible((prevValue) => prevValue + 5);
    }

    const [activities, setactivities] = useState([]);
    const [filterchanged, setfilterchanged] = useState(1)

    const hitFilter = (index) => {
        let tmpdata = dataFilter;
        tmpdata[index].state = !dataFilter[index].state;
        setdataFilter(tmpdata)
        setfilterchanged(Math.random()/9 + 1)
    }

    const clearFilter = () => {
        let tmpdata = dataFilter;
        for (let i = 0; i < tmpdata.length; i++) {
            tmpdata[i].state = false
        }
        setdataFilter(tmpdata)
        setfilterchanged(Math.random()/9 + 1)
    }

    const beautifyAddr = (addr) => {
        if(addr ==  null)
            return ''
        if(addr.length > 12){
            let tmpstr = addr.slice(0,4) + '...' + addr.slice(addr.length - 6, addr.length)
            return tmpstr;
        }
        return addr
    }

    const beautifyType = (type) => {
        let value;
        switch (type) {
            case 'nft_transfer':
                value = 'Transfer';
                break;
        
            default:
                break;
        }
        return value;
    }

    const beautifyTime = (timestr) => {
        let tmpdatatime = new Date(timestr)
        let curtime = new Date();
        let value;
        if(tmpdatatime == null || tmpdatatime == '')
            return ''
        if(tmpdatatime.getDate() === curtime.getDate()
            && tmpdatatime.getFullYear() === curtime.getFullYear()
            && tmpdatatime.getMonth() === curtime.getMonth() ){
            var diff =(curtime. getTime() - tmpdatatime. getTime()) / 1000;
            diff /= (60 * 60);
            return (Math. abs(Math. round(diff))+1).toString() + ' hours ago';
        }
        return timestr.split('T')[0];
    }

    useEffect( async () => {
        let initdata = await axios.get(`${process.env.REACT_APP_API_URL}/activities`, {
            params: {
            },
        })
        console.log('init data', initdata)
        if(initdata?.data.data != null){
            setactivities(initdata.data.data);
        }
    },[]);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-md-12">
                            <div className="page-title-heading mg-bt-12">
                                <h1 className="heading text-center">Activity</h1>
                            </div>
                        </div>
                    </div>
                </div>                    
            </section>
            <section className="tf-activity s1 tf-section">
                <div className="themesflat-container">
                    <div className="row">
                        <div className="col-lg-8 col-md-8 col-12">
                            <div className="table-ranking">
                                <div className="flex th-title">
                                    <div className="column1">
                                        <h3>Title</h3>
                                    </div>
                                    <div className="column">
                                        <h3>Price</h3>
                                    </div>
                                    <div className="column">
                                    <h3>From</h3>
                                    </div>
                                    <div className="column">
                                    <h3>To</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Time</h3>
                                    </div>
                                    <div className="column">
                                    <h3>Type</h3>
                                    </div>
                                </div>
                            {
                                data.slice(0,visible).map((item,index) => (
                                    <div key={index} className="fl-item2">
                                        <div className="item flex">
                                            <div className="infor-item flex column1">
                                                <div className="media d-flex">
                                                    <img className="rounded" style={{ objectFit: 'cover' }}  src={item?.img} alt="Axies" />
                                                </div>
                                                <div className="content-collection align-self-center">
                                                    <h5 className="title"><Link to="/">{item?.title}</Link></h5>
                                                </div>
                                            </div>
                                            <div className="column">
                                                <span>{item.price? parseFloat(item.price).toFixed(2) : ''} </span>
                                            </div>
                                            <div className="column td2">
                                                <span><Link to="/" className='text-success'>{ beautifyAddr(item.from) }</Link></span>
                                            </div>
                                            <div className="column td3">
                                                <span><Link to="/" className='text-danger'>{ beautifyAddr(item.to) }</Link></span>
                                            </div>
                                            <div className="column td4">
                                                <span><small>{ beautifyTime(item.issued_at) }</small></span>
                                            </div>
                                            <div className="column td5">
                                                <span>&nbsp;&nbsp;&nbsp;&nbsp;{ beautifyType(item.type) }</span>
                                            </div>
                                        </div>
                                    </div>
                                ))
                            }
                            {
                                visible < data.length && 
                                <div className="col-md-12 wrap-inner load-more text-center"> 
                                    <Link to="#" id="load-more" className="sc-button loadmore fl-button pri-3 mt-10" onClick={showMoreItems}><span>Load More</span></Link>
                                </div>
                            }
                            </div>
                        </div>
                        <div className="col-lg-4 col-md-4 col-12">

                            <div id="side-bar" className="side-bar style-2">
                                <div className="widget widget-search mgbt-24">
                                    <InputGroup className="mb-3">
                                        <FormControl
                                        placeholder="Recipient's username"
                                        aria-label="Recipient's username"
                                        aria-describedby="basic-addon2"
                                        />
                                        <Button variant="outline-secondary" id="button-addon2">
                                        Button
                                        </Button>
                                    </InputGroup>
                                </div>

                                <div className="widget widget-filter style-2 mgbt-0">
                                    <h3 className="title-widget">Filter</h3>
                                    <ul className="box-check">
                                        {
                                            filterchanged != 0
                                             && dataFilter.map((item,index) => (
                                                !item.state ?
                                                <li key={index} onClick={() => hitFilter(index)}><a className="box-widget-filter" style={{ cursor: 'pointer'}}><i className={item.icon}></i>{item.name}</a></li>
                                                :
                                                <li key={index} onClick={() => hitFilter(index)}><a className="box-widget-filter active" style={{ cursor: 'pointer'}}><i className={item.icon}></i>{item.name}</a></li>
                                            ))
                                        }
                                    </ul>
                                    <a onClick={clearFilter} className="clear-check btn-filter style-2" >
                                        Clear All Filters
                                    </a>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
            <Footer />
        </div>
    );
}

export default MyActivity;
