import React, { useEffect, useState, useCallback } from 'react';
import { Link, useParams } from 'react-router-dom'
import axios from 'axios';

const ItemHistory = ({contract_id, token_id}) => {
    const [activity, setActivity] = useState([])
    const getActivities = useCallback(async () => {
        if(!contract_id || !token_id) return;
        try {
            const resActivity = await axios.get(`${process.env.REACT_APP_API_URL}/activities`, {
                params: {
                    nft_contract_id: contract_id,
                    token_id: token_id
                },
		    })

            setActivity(resActivity.data.data.results);
    
        } catch (error) {
            console.log(error);
            setActivity([]);
        }
    },[contract_id, token_id]);

    useEffect(() => {
        if(!contract_id || !token_id) return;
        getActivities();
    }, [contract_id, token_id]);

    return (
        <ul className="bid-history-list">
        {
            activity.length>0&&activity.map((item, index) => (
                <li key={index} item={item}>
                    <div className="content">
                        <div className="client">
                            <div className="sc-author-box style-2">
                                
                                <div className="author-infor">
                                    <div className="name">
                                        <h6><Link to="/author-02">{item.from} </Link></h6> <span> listed NFT</span>
                                    </div>
                                    <span className="time">{item.issued_at}</span>
                                </div>
                            </div>
                        </div>
                        <div className="price">
                            <h5>{item.price}</h5>
                            <span>= {""}</span>
                        </div>
                    </div>
                </li>
            ))
        }
        </ul>
    );
}

export default ItemHistory;
