import React, { useEffect, useState } from 'react';
import { Link, useParams } from 'react-router-dom'
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'
import axios from 'axios';
import Header from '../components/header/Header';
import Footer from '../components/footer/Footer';
import Explore from '../components/layouts/explore-04/Explore';
import Chart from 'react-apexcharts';
import { ButtonGroup, ToggleButton } from 'react-bootstrap';
import defaultBannerUrl from '../assets/images/backgroup-secsion/option1_bg_profile.jpg';
import defaultPhotoUrl from '../assets/images/avatar/avt-2.jpg';

const Explore04 = () => {
    const {collectionId} = useParams();
    const [collectionInfo , setCollectionInfo] = useState(null);

    const radios = [
        { name: '7 days', value: '1' },
        { name: '30 days', value: '2' },
        { name: '90 days', value: '3' },
        { name: '1 year', value: '4' },
        { name: 'all time', value: '5' },
    ];
    
    const [radioValue, setRadioValue] = useState('1');

    const [series, setseries] = useState([{
        name: 'Price',
        type: 'area',
        data: [23, 42, 35, 27, 43, 22, 17, 31, 22, 22, 12, 16],
    },
    {
        name: 'Volume',
        type: 'column',
        data: [4, 5, 41, 671, 27, 13, 20, 12, 22, 32, 25, 16],
    }])
    
    const [options, setoptions] = useState(
    {
        chart: {
          height: 350,
          type: 'line',
          toolbar: {
            show: false,
          },
          zoom:{
              enabled: false,
          }
        },
        stroke: {
            show: true,
            curve: 'smooth',
            lineCap: 'butt',
            colors: undefined,
            width: 1,
            dashArray: 0,   
        },
        fill: {
            type: 'gradient',
            gradient: {
                shade: 'dark',
                type: "vertical",
                shadeIntensity: 0.1,
                gradientToColors: undefined, // optional, if not defined - uses the shades of same color in series
                inverseColors: true,
                opacityFrom: 1,
                opacityTo: 0.1,
                stops: [0, 70, 100],
                colorStops: []
            }
        },
        title: {
          text: 'Floor Price & Volume (Near)'
        },
        dataLabels: {
          enabled: false,
          enabledOnSeries: [1]
        },
        labels: ['01 Jan 2001', '02 Jan 2001', '03 Jan 2001', '04 Jan 2001', '05 Jan 2001', '06 Jan 2001', '07 Jan 2001', '08 Jan 2001', '09 Jan 2001', '10 Jan 2001', '11 Jan 2001', '12 Jan 2001'],
        xaxis: {
            type: 'datetime',
            labels: {
                style: {
                    colors: '#7A798A'
                }
            }
        },
        yaxis: [{
            title: {
                text: '',
            },
            labels: {
                style: {
                    colors: '#7A798A'
                }
            }
        }, {
            // show:false,
            opposite: true,
            title: {
                text: '',
                logBase: 10
            },
            labels: {
                style: {
                    colors: '#7A798A'
                }
            }
        }],
        colors: ['#008FFB','#c0e4ff'],
        theme: {
            mode: 'light', 
        }
    })

    const getCollectionStats = async () => {
        try {
            const resCollectionStat = await axios.get(`${process.env.REACT_APP_API_URL}/collection-stats`, {
                params: {
                    nft_contract_id: collectionId
                },
		    })

            setCollectionInfo(resCollectionStat.data.data);
        } catch (error) {
            console.log(error);
            setCollectionInfo(null);
        }
    };

    useEffect(() => {
        getCollectionStats();
    }, []);

    return (
        <div>
            <Header />
            <section className="flat-title-page inner pb-0">
                <div className="overlay"></div>
                <div className="themesflat-container">
                    <div className="flat-tabs tab-authors">
                        <div className="author-profile flex" style={{backgroundImage:`url(${collectionInfo?.bgimg_url? collectionInfo.bgimg_url : defaultBannerUrl})`}}>
                        </div>
                        <div className="sc-author-box style-2 feature-profile d-flex justify-content-center"><img src={collectionInfo?.banimg_url? collectionInfo.banimg_url: defaultPhotoUrl} alt="Axies" className="mysuperavatar avatar"/>
                            {
                                collectionInfo?.is_verified ? 
                                <div className='author-avatar' style={{ zIndex: 41, marginBottom: -40 }}>
                                    <div className="badge"></div>
                                </div>
                                :
                                <></>
                            }
                        </div>
                        <div className="widget-social style-3 d-flex justify-content-end mysocial">
                            <ul>
                                <li><a href={collectionInfo?.website_url} target="_blank"><i className="fa fa-globe"></i></a></li>
                                <li><a href={collectionInfo?.twitter_url} target="_blank"><i className="fab fa-twitter"></i></a></li>
                                <li><a href={collectionInfo?.discord_url} target="_blank"><i className="fab fa-facebook"></i></a></li>
                            </ul>
                        </div>
                        <div className='mb-5'>
                            <h2 className="title text-center mt-4 mb-4 text-white">{collectionInfo?.name}</h2>
                            <p className="content text-center text-white">{collectionInfo?.description}</p>
                        </div>
                        <div className='row'>
                            <div className='col-sm-0 col-md-2'></div>
                            <div className='row p-5 col-md-8'>
                                <div className='col-6 col-sm-3 col-md-3'>
                                    <div className='sc-card-product' style={{ borderRadius: 7}}>
                                        <p className='text-center mb-2'><small>FLOOR PRICE</small></p>
                                        <p className='text-center'>{collectionInfo?.floor_price || 0} </p>
                                    </div>
                                </div>
                                <div className='col-6 col-sm-3 col-md-3'>
                                    <div className='sc-card-product' style={{ borderRadius: 7}}>
                                        <p className='text-center mb-2'><small>TOTAL VOLUME</small></p>
                                        <p className='text-center'>{collectionInfo?.total_volume || 0} </p>
                                    </div>
                                </div>
                                <div className='col-6 col-sm-3 col-md-3'>
                                    <div className='sc-card-product' style={{ borderRadius: 7}}>
                                        <p className='text-center mb-2'><small>TOTAL ITEMS</small></p>
                                        <p className='text-center'>{collectionInfo?.total_count}</p>
                                    </div>
                                </div>
                                <div className='col-6 col-sm-3 col-md-3'>
                                    <div className='sc-card-product' style={{ borderRadius: 7}}>
                                        <p className='text-center mb-2'><small>SALES ITEMS</small></p>
                                        <p className='text-center'>5</p>
                                    </div>
                                </div>
                            </div>
                            <div className='col-sm-0 col-md-2'></div>
                        </div>
                    </div>
                </div>                    
            </section>
            <Tabs defaultActiveKey="items" id="uncontrolled-tab-example" className="mb-3">
                <Tab eventKey="items" title="Items">
                    <Explore data={collectionInfo?.attributes||[]} contract_id={collectionInfo?.contract_id||''}/>
                </Tab>
                <Tab eventKey="activity" title="Activity">
                    <div className='m-5 p-5'>
                        <Chart options={options} series={series} type='line' height={450} />
                        <ButtonGroup className='m-5'>
                            {radios.map((radio, idx) => (
                            <ToggleButton
                                key={idx}
                                id={`radio-${idx}`}
                                type="radio"
                                variant={'outline-primary'}
                                name="radio"
                                value={radio.value}
                                checked={radioValue === radio.value}
                                onChange={(e) => setRadioValue(e.currentTarget.value)}
                                className='p-3'
                            >
                                <big>{radio.name}</big>
                            </ToggleButton>
                            ))}
                        </ButtonGroup>
                    </div>
                </Tab>
            </Tabs>
            
            <Footer />
        </div>
    );
}

export default Explore04;
